.footer_outer_container {
        background-color: #111; 
        color: gray;
        display: flex;
        justify-content: center;
     } 
     
     .footer_inner_container {
        width: 60%;
     }  
     
     .footer_icons {
         width: 12%;
         display: flex;
         justify-content: space-between;
         margin-left: 10px;
         color: white;
     }
     
     .footer_data {
         display: flex;
         justify-content: space-between;
         margin-left: 10px;
         color: #808080;
     }
     
     .footer_data ul {
         list-style: none;
         display: flex;
         flex-direction: column;
         justify-content: space-around;
         height: 12vh;
     }
     
     .footer_data ul li {
         cursor: pointer;
     }
     
     .service_code {
         display: flex;
         align-items: center;
         justify-content: center;
         border: #fff 1px solid;
         width: 15%;
         margin: 10px 0;
         cursor: pointer;
    
     }
     
     .copy_write {
         font-size: 12px;
         margin-bottom: 10px;
     }
     .copy_write {
        font-size: 12px;
        margin-bottom: 10px;
        color: #808080; 
      }
      
      .copy_write::before {
        content: "\00a9"; /* copyright symbol */
        color: #fff; 
        
      }
      
