.header_outer_container{
    position:sticky;
    top:0;
    display:flex;
    justify-content: center;
    align-items: center;
background-color: rgb(148, 132, 132);
background-color:rgb(20, 20, 20);
/* background-color:  transparent; */
height:10vh ;
color: white;
transition: all 0.5s;
transition-timing-function:ease-in;  z-index: 1;
/* background-color: red; */
}
.nav_black{
    background-color:rgb(20, 20,20 );
}
.header_container{
    width: 90%; 
    display:flex ;
    justify-content:space-between ;
    align-items: center;
}

.header_left ul {
    list-style-type: none;
    display: flex;
    margin: 0;
    padding: 0;
}

.header_left li {
    margin-right: 20px;
    cursor: pointer;
}  

.header_right ul {
    list-style-type: none;
    display: flex;
    margin: 0;
    padding: 0;
}

.header_right ul li {
    margin-right: 20px;
    cursor: pointer;
    display: flex;
}



